.button{display:inline-flex;align-items:center;justify-content:center;width:3.2rem;height:3.2rem;aspect-ratio:1/1;padding:.8rem;border:2px solid #fff;border-radius:50%;background:rgba(0,0,0,0);color:#fff;font-family:"Inter",sans-serif;font-weight:600;transition:all .2s ease;cursor:pointer}.button:hover{background-color:var(--hover-bg);color:#000;border-color:var(--hover-bg)}.icon{font-size:1.5rem;display:flex;align-items:center;justify-content:center}